import React from "react";
import "../css/calculator.css";

const Calculator = () => {
	// let output=1;
	const [output, setoutput] = React.useState('')
	const myfunction = (name) => {
		alert('hi' + name);
	}
	const myfunction1 = (Welcome) => {
		alert("UI development");
	}
	const display = (input) => {
		// output += input;
		setoutput(output + input)
		// console.log(output);
	}
	const clear = () => {
		setoutput('');
	}
	const calculate = ()=>{
		let result= eval(output);
		setoutput(String(result))
		
	}
	const deletefn = () => {
		// setoutput(output/10 | 0);
		let outputarray = output.split('');
		// console.log(outputarray);
		if(outputarray.length >0){
			outputarray.pop();
			// console.log(outputarray);
		}
		// console.log(outputarray);
		setoutput(outputarray.join(''))
	}
	return (
		<div class="calculator">
			<input type="text" placeholder="0" value={output} />
			<button onClick={() => display('%')}>%</button>
			<button onClick={() => deletefn()}>Del</button>
			<button onClick={() => clear('')}>Clr</button>
			<button onClick={() => display('7')}>7</button>
			<button onClick={() => display('/')}>/</button>
			<button onClick={() => display('8')}>8</button>
			<button onClick={() => display('9')}>9</button>
			<button onClick={() => display('4')}>4</button>
			<button onClick={() => display('*')}>*</button>
			<button onClick={() => display('5')}>5</button>
			<button onClick={() => display('6')}>6</button>
			<button onClick={() => display('1')}>1</button>
			<button onClick={() => display('-')}>-</button>
			<button onClick={() => display('2')}>2</button>
			<button onClick={() => display('3')}>3</button>
			<button onClick={() => display('0')}>0</button>
			<button onClick={() => display('+')}>+</button>
			<button onClick={() => display('.')}>.</button>
			<button onClick={() => calculate()} class="equal">=</button>
			{/* <button onClick={() => myfunction1('UI')} class="equal">=</button> */}
		</div>

	)
}

export default Calculator;