import React from "react";
import "../css/signup.css";
import { useForm } from "react-hook-form";

const Signup = () => {
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    console.log(data);
  };
  return (
    <form className="signupform" onSubmit={handleSubmit(onSubmit)}>
      <div className="form-group">
        <label htmlFor=""> Username</label>
        <input type="text" name="Username" {...register("user",{
            required: {
                value:true,
                message:'username is required'
            },
            minLength: {
                value:3,
                message: 'username should have atleast 3 characters'
            }
        })} />
        <small>
        {errors.user?.message}
        </small>
      </div>

      <div className="form-group">
        <label htmlFor="">Password</label>
        <input type="text" {...register("password",{
            required: {
                value:true,
                message:'password is required'
            },
            minLength: {
                value:6,
                message: 'password should have contain atleast 6 characters'
            },
            pattern: {
                value:/^(?=.*[a-z])(?=.*[0-9])[a-z0-9]{6,}$/,
                message: 'password should have one small letter and number'
            }
        })} />
        <small>
            {errors.password?.message}
            </small>
      </div>
      <div className="form-group">
        <label htmlFor="">Confirm Password</label>
        <input type="password" {...register("confirmpassword",{
            required: {
                value:true,
                message:'password is required'
            },
            minLength: {
                value:6,
                message: 'password should have contain atleast 6 characters'
            }
        })} />
        <small>
            {errors.confirmpassword?.message}
        </small>
      </div>
      <div className="form-group" style={{ textAlign: "center" }}>
        <button>Sign UP</button>
      </div>
    </form>
  );
};

export default Signup;
