import React from "react";
import "../css/card.css";
import profile from "../assets/employee.png";

const Card = (props) => {
    return (
        
            <div className="card">
                <img src={profile} alt="" width="200px" />
                <p> Name:{props.emp.Name}</p>
                <p> ID:{props.emp.EmployeeID}</p>
                <button onClick={props.btnfn}>{props.btnname}</button>
            </div>
        
    )
}

export default Card;
