import React from "react";
import Header from "./header";
import Tabledata from "./tabledata";
import "../css/pagenation.css"; 

const Table = (props) => {
    const [currentPage, setCurrentPage] = React.useState(1);
    const [lastPage, setLastPage] = React.useState(1);
    const [pageLimit, setPageLimit] = React.useState(15);
    const displayPage = (page) => {
      let start = (page - 1) * pageLimit;
      let end = start + pageLimit;
      let filterData = props.data.slice(start, end);
      return filterData.map((tableDetail, index) => {
        return <Tabledata key={index} data={tableDetail}></Tabledata>;
      });
    };
    const generateButtons = () => {
      let buttons = [];
      let start = currentPage - 2 > 1 ? currentPage - 2 : 1;
      let end = currentPage + 2 < lastPage ? currentPage + 2 : lastPage;
      for (let i = start; i <= end; i++) buttons.push(i);
      return buttons.map((each) => (
        <button
          className={currentPage === each ? "active" : ""}
          onClick={() => setCurrentPage(each)}
        >
          {each}
        </button>
      ));
    };
    React.useEffect(() => {
      let myLastPage = Math.ceil(props.data.length / pageLimit);
      setLastPage(myLastPage);
    }, []);
    return (
      <div>
        <div className="buttons">
          {/* <button onClick={() => setCurrentPage(2)}>2</button>
          <button onClick={() => setCurrentPage(3)}>3</button>
          <button onClick={() => setCurrentPage(4)}>4</button>
          <button onClick={() => setCurrentPage(5)}>5</button> */}
          <button onClick={() => setCurrentPage(1)}>&lt;&lt;</button>
          <button
            onClick={() => setCurrentPage(currentPage - 1)}
            disabled={currentPage === 1}
          >
            &lt;
          </button>
          {generateButtons()}
          <button
            onClick={() => setCurrentPage(currentPage + 1)}
            disabled={currentPage === lastPage}
          >
            &gt;
          </button>
          <button onClick={() => setCurrentPage(lastPage)}>&gt;&gt;</button>
          <select name="" id="" value={pageLimit} onChange={(e) => setPageLimit(parseInt(e.target.value))}>
            <option value={5}>5</option>
            <option value={10}>10</option>
            <option value={15}>15</option>
            <option value={20}>20</option>
          </select>
        </div>
        <table>
          <Header data={props.data[0]}></Header>
          <tbody>{displayPage(currentPage)}</tbody>
        </table>
      </div>
    );
  };
