import React from "react";
import "../css/navbar.css";
import {Link, NavLink} from "react-router-dom";

const Navbar = () => {
    return (
        <nav>
            <ul>
                <li><a href="/"><i class="fa-solid fa-house"></i> home</a></li>
                <li><Link to="/login"><i class="fa-solid fa-arrow-right-to-bracket"></i> login</Link></li>
                <li><NavLink to="/signup"><i class="fa-solid fa-user-plus"></i> signup</NavLink></li>
                <li><NavLink to="/calculator"><i class="fa-solid fa-calculator"></i> calculator</NavLink></li>
                <li><NavLink to="/employee"><i class="fa-solid fa-user-group"></i> employee</NavLink></li>
                <li><NavLink to="/pagenation"><i class="fa-solid fa-list"></i> pagenation</NavLink></li>

            </ul>
        </nav>
    )
}

export default Navbar;