import React from "react";
import Card from "../components/card";
import "../css/employeedetail.css";
import { useNavigate } from "react-router-dom";
import { useSelector } from 'react-redux';

const Empdetail = () => {
    const navigate = useNavigate();
    // let employee = {
    //     "Name": "Aurora",
    //     "Age": 53,
    //     "Email": "ante.blandit@disparturient.ca",
    //     "Date of joining": "10/06/2019",
    //     "Phone number": "1628101902299",
    //     "Street": "493 Iaculis Rd.",
    //     "City": "Lewiston",
    //     "Zip": "42591-180",
    //     "Region": "Maine",
    //     "Country": "Sudan",
    //     "Info": "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    //     "EmployeeID": 101
    // };
    // employee = JSON.parse(localStorage.getItem('emp'));
    const employee=useSelector((state)=>state.emp.emp)
    const back = () => {
        // alert('Back')
        navigate('/employee');
    }
    const generatetabledetail = () => {
        let keys=Object.keys(employee);
        return keys.map(e => (
            <tr>
                <th>{e}</th>
                <td>{employee[e]}</td>
            </tr>
        ))
    }
    return (
        <div className="Empdetail">
            <Card emp={employee} btnfn={() => back()} btnname="back"></Card>
            <table>
            <tbody id="data">
                <tr>
                    <th>Heading</th>
                    <td> Data</td>
                </tr>
                {generatetabledetail()}
            </tbody>
        </table>
        </div>
    )
}

export default Empdetail; 